<template>
<div>

  <input type="id" placeholder="input your id" v-model="id">
  <input type="password" placeholder="input your password" v-model="password">

  <button  @click="Success"></button>
  <button @click="join"></button>
  <button @click="LoginSuccess">  </button>
  <button @click="LoginFail">  </button>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'HelloWorld',
  data () {
    return {
      id:"",          //
      password:"",     //
      result: ""
    }
  },
  methods: {
   Success() {
     this.result = axios.get('signIn/into?id='+this.id+'&password='+this.password)
     console.log(this.result)
   },
    join() {
     axios.get('signup/save?id='+this.id+'&password='+this.password)
      alert(' .')
    },
    LoginSuccess() {
        this.$router.push('/LoginSuccess')
      },
      LoginFail(){
        this.$router.push('/Loginfail')
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



